.FixedBar {
  height: 60px;
  width: 100%;
  bottom: 0;
  position: absolute;
  position: fixed;
  left:0;
  z-index:999;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  border-top: solid 1px #ececec;
  background: white;
}
.fixed-left {
  -ms-flex: 1 1;
      flex: 1 1;

  height: 100%;
}
.fixed-middle {
  -ms-flex: 2 1;
      flex: 2 1;
  padding-right: 0.5rem;
}
.fixed-right {
  -ms-flex:1 1;
      flex:1 1;
  height: 100%;

}
.checkout-button {
  background: red;
  height: 100%;
  padding-top: 1rem;
  color: white;
  font-weight: bold;
  cursor: pointer;
}
.fixed-middle {
  text-align: right;

}
.fixed-total {
  font-size: 14px;
}
.fixed-total label {
  color: red;
  font-weight: bold;
}
.fixed-tax {
  font-size: 12px;
  color :gray;
}



